body {
  color: white;
  background-color: black;
  font-family: monospace;
  font-size: 1.2rem;
  line-height: 1.7;
}

.wrapper {
  width: fit-content;
  max-width: 800px;
  margin: 4rem auto;
  padding: 0 1rem;
}

h1 {
  line-height: 2rem;
}

hr {
  text-align: center;
  border: 0;

  &:before {
    content: '/////'
  }

  &:after {
    content: attr(data-content) '/////'
  }
}

table,
th,
td {
  border: thin solid black;
  border-collapse: collapse;
}

code,
pre {
  color: #fff;
  font-family: monospace;
  background-color: #292929;
  font-size: .9rem;
  margin: 1rem 0;
  white-space: pre-wrap
}

div.highlighter-rouge code {
  display: block;
  overflow-x: auto;
  padding: 1rem
}

blockquote {
  font-style: italic;
  border: thin solid #000;
  padding: 1rem
}

blockquote p {
  margin: 0
}

a {
  color: #ff0;
  text-decoration: none
}

mark {
  background-color: #fff;
  color: #000
}